.circle-button {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    color: #9e7d7b;
    transition: opacity .3s cubic-bezier(.25,.46,.45,.94),color .3s cubic-bezier(.25,.46,.45,.94)
}

.circle-button:before {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    content: "";
    background-color: #fff;
    border-radius: 50%;
    transition: transform .6s cubic-bezier(.19,1,.22,1),background-color .3s cubic-bezier(.25,.46,.45,.94)
}

.circle-button[disabled=true] {
    pointer-events: none;
    opacity: .5
}

.circle-button * {
    pointer-events: none
}

.circle-button .svg {
    fill: currentColor;
    transition: fill .3s cubic-bezier(.25,.46,.45,.94)
}

.circle-button__inner {
    height: 40px;
    width: 40px;
    position: relative;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center
}

.circle-button__inner:after {
    position: absolute;
    top: -4px;
    left: -4px;
    height: calc(100% + 8px);
    width: calc(100% + 8px);
    content: "";
    border: 1px solid rgba(255,255,255,.4);
    border-radius: 50%;
    transition: transform .6s cubic-bezier(.19,1,.22,1),border-color .3s cubic-bezier(.25,.46,.45,.94)
}

.circle-button__label {
    position: absolute;
    top: calc(100% + 25px);
    left: 50%;
    text-align: center;
    transform: translate(-50%);
    opacity: 0;
    transition: opacity .4s cubic-bezier(.19,1,.22,1);
    pointer-events: none
}

