.site-wrapper {
    position: relative;
    overflow-x: hidden;
    overflow-y: auto;
    width: 100%;
    height: 100vh
}

.u-visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0 0 0 0);
    border: 0
}

@keyframes fade-in {
    0% {
        opacity: 0
    }

    to {
        opacity: 1
    }
}

@keyframes fade-out {
    0% {
        opacity: 1
    }

    to {
        opacity: 0
    }
}

@keyframes fade-y-in {
    0% {
        opacity: 0;
        transform: translateY(30px)
    }

    to {
        opacity: 1;
        transform: translateY(0)
    }
}

@keyframes fade-y-out {
    0% {
        opacity: 1;
        transform: translateY(0)
    }

    to {
        opacity: 0;
        transform: translateY(30px)
    }
}

@keyframes title-in {
    0% {
        transform: translateY(80%) scale(.3);
        opacity: 0
    }

    90% {
        opacity: 1
    }

    to {
        opacity: 1;
        transform: translateY(0) scale(1)
    }
}

@keyframes title-out {
    0% {
        opacity: 1;
        transform: translateY(0) scale(1)
    }

    90% {
        opacity: 0
    }

    to {
        transform: translateY(80%) scale(.3);
        opacity: 0
    }
}

.fade-short-enter-active,.fade-short-leave-active {
    transition: opacity .1s cubic-bezier(.25,.46,.45,.94)
}

.fade-short-enter-from,.fade-short-leave-to {
    opacity: 0
}

.fade-y-enter-active,.fade-y-leave-active {
    transition: opacity .3s cubic-bezier(.25,.46,.45,.94),transform .3s cubic-bezier(.25,.46,.45,.94)
}

.fade-y-enter-from,.fade-y-leave-to {
    opacity: 0;
    transform: translateY(40px)
}

.fade-enter-active,.fade-leave-active {
    transition: opacity .3s cubic-bezier(.455,.03,.515,.955)
}

.fade-enter-from,.fade-leave-to {
    opacity: 0
}

.fade-delay-enter-active {
    transition: opacity .3s .3s cubic-bezier(.455,.03,.515,.955)
}

.fade-delay-leave-active {
    transition: opacity .3s .2s cubic-bezier(.455,.03,.515,.955)
}

.fade-delay-enter-from,.fade-delay-leave-to {
    opacity: 0
}

